.section-title {
  font-size: 40px;
  font-family: $accent-font;
  font-weight: bold;

  &--gallery {
    font-weight: normal;
    font-style: italic;
    color: $accent-color;
    text-align: right;
    width: 1215px;
    margin: 0 auto;
    @include fields();
  }

  &--catalog {
    background: $green-color;
    padding: 10px 50px;
    border: 2px solid #000;
  }
}
