.callback {
  text-align: center;
  background-color: #fff;
  width: 40%;
  height: auto;
  position: absolute;
  top: 20%;
  left: 50%;
  margin-left: -20%;
  padding: 30px;
  border: 3px solid #000;
  opacity: 0;
  transform: translateY(-200px);
  opacity: 0;
  z-index: -1;

  &.open {
    opacity: 1;
    z-index: 101;
    transform: translateY(0);
    transition: 1s;
  }

  & h3 {
    font-size: 30px;
    text-transform: uppercase;
    margin-bottom: 30px;
    font-family: "Franklin Gothic Medium";
  }
}

.callback__form {
  text-align: center;
}

.callback__item {
  margin-bottom: 30px;
  width: 95%;

  & label {
    display: block;
    width: 100%;
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    font-family: "Franklin Gothic Medium";
  }

  & textarea {
    width: 100%;
    border: none;
    border-bottom: 2px solid #000;
    padding: 10px 15px;
    background-color: transparent;

    &:hover,
    &:active {
      box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5)
    }

    &:focus {
      border: none;
      border-bottom: 2px solid grey;
      background-color: transparent !important;
      outline: none;
    }
  }
}

.callback__input {
  margin-bottom: 30px;

  & label {
    display: block;
    width: 100%;
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    font-family: "Franklin Gothic Medium";
  }

  & input {
    width: 100%;
    height: 30px;
    border: none;
    border-bottom: 2px solid #000;
    padding: 10px 15px;
    background-color: transparent;

    &:hover,
    &:active {
      box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
    }

    &:focus {
      border: none;
      border-bottom: 2px solid grey;
      background-color: transparent !important;
      outline: none;
    }
  }
}

.callback__clouse {
  float: right;
  background: none;
  border: none;
  font-weight: bold;

  &:hover,
  &:focus,
  &:active {
    outline: none;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.5)
  }
}

.loading p {
  margin: auto;
  font: 20px reg;
}

.loading {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
}

@keyframes blink {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}

.loading p {
  animation: blink 0.7s infinite forwards ease-in-out;
}

.animload {
  z-index: 1;
  opacity: 0.8;
}

.error {
  background: #DC352F;
}

.success {
  background: #36AE46;
}

.msg {
  width: 340px;
  position: absolute;
  padding: 25px 15px;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 0;
  font: 16px med;
  color: #ffffff;
  text-align: center;
  border-radius: 7px;
  opacity: 0;
}

.msgbox {
  position: absolute;
  bottom: 0;
  width: 100%;
}

@keyframes shake {
  from {transform: translateX(0px);}
  50% {transform: translateX(3px);}
  to {transform: translateX(-3px);}
}

.inputerror {
  border-color: #DC352F !important;
  animation: shake 0.15s 3 forwards ease-in-out;
}
