.catalog {
  position: relative;
  width: 100%;
  padding-bottom: 70px;

  &::after {
    content: "";
    width: 100%;
    height: 30px;
    background-color: $green-color;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 2px solid #000;
  }
}

.catalog__list {
  list-style: none;
  margin: 20px 30px 80px;
}

.catalog__item {
  color: $accent-color;
  font-size: 24px;
  font-weight: bold;
}

.catalog__columns {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.catalog__img {
  max-width: 50%;
  margin-top: -50px;

  & img {
    max-width: 100%;
    height: auto;
  }
}
