.banner {
  background: url("../img/banner.jpg") no-repeat center /cover;
  @include fields();
  text-align: center;
  max-width: 100%;
  margin-bottom: 50px;
  min-height: 460px
}

.banner__text {
  font-family: $accent-font;
  @include fields();
  font-size: 60px;
  font-weight: bold;
  color: $accent-color;
  text-shadow:0px 1px 0 #000,
    0px -1px 0 #000,
    1px 0px 0 #000,
    -1px 0px 0 #000,
    1px 1px 0 #000,
    1px -1px 0 #000,
    -1px 1px 0 #000,
    -1px -1px 0 #000,
    0px 2px 0 #000,
    0px -2px 0 #000,
    2px 0px 0 #000,
    -2px 0px 0 #000,
    2px 2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    -2px -2px 0 #000;
}
