/*Шрифты*/
@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap; }

@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia-BoldItalic.woff") format("woff");
  font-style: bold;
  font-weight: bold;
  font-display: swap; }

@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia-Italic.woff") format("woff");
  font-style: italic;
  font-weight: normal;
  font-display: swap; }

/*Базовые стили*/
body {
  font-size: 18px;
  font-family: "Arial", sans-serif;
  color: #000;
  margin: 0;
  padding: 0;
  min-width: 1300px;
  line-height: 1.2; }

a {
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 12px;
  max-width: 1300px;
  margin: 50px auto 52px;
  padding-left: 40px;
  padding-right: 40px; }

.page-header__link {
  display: flex;
  justify-content: space-between;
  width: 60%;
  align-items: baseline;
  margin-left: 20px; }

.page-header__logo {
  margin-bottom: 13px; }
  .page-header__logo--text {
    margin-bottom: 4px; }
  .page-header__logo:hover, .page-header__logo:focus {
    opacity: 0.8; }
  .page-header__logo:active {
    opacity: 0.6; }

.banner {
  background: url("../img/banner.jpg") no-repeat center/cover;
  padding-left: 40px;
  padding-right: 40px;
  text-align: center;
  max-width: 100%;
  margin-bottom: 50px;
  min-height: 460px; }

.banner__text {
  font-family: "Georgia", sans-serif;
  padding-left: 40px;
  padding-right: 40px;
  font-size: 60px;
  font-weight: bold;
  color: #6a3a0c;
  text-shadow: 0px 1px 0 #000, 0px -1px 0 #000, 1px 0px 0 #000, -1px 0px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, -1px -1px 0 #000, 0px 2px 0 #000, 0px -2px 0 #000, 2px 0px 0 #000, -2px 0px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, -2px -2px 0 #000; }

.button {
  font-family: "Georgia", sans-serif;
  display: inline-block;
  text-align: center;
  padding: 12px 30px;
  color: #ffffff;
  background-color: #e2b776;
  text-decoration: none;
  text-transform: uppercase;
  font-style: italic;
  margin-left: 50px; }
  .button:hover, .button:focus {
    background-color: #85bf8f;
    outline: none; }
  .button:active {
    color: rgba(255, 255, 255, 0.302); }

.page-footer {
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 35px;
  padding-bottom: 35px;
  margin-top: -3px;
  max-width: 1300px;
  margin: 0 auto; }

.page-footer__contacts {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 150px; }

.page-footer__inn {
  font-size: 16px;
  color: grey; }

.menu {
  display: flex;
  width: auto;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0; }

.menu__item {
  margin-left: 33px;
  padding-bottom: 5px; }

.menu__link {
  font-size: 20px;
  color: #000; }
  .menu__link:hover, .menu__link:focus {
    color: rgba(0, 0, 0, 0.6); }
  .menu__link:active {
    color: rgba(0, 0, 0, 0.3); }
  .menu__link--active-front {
    border-color: #000; }

.main-nav__menu {
  padding: 0;
  margin: 0;
  position: relative;
  display: none; }

.main-nav__toggle {
  background: none;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 18px;
  padding: 0;
  margin-bottom: 16px;
  margin-right: 20px;
  position: relative;
  outline: none; }
  .main-nav__toggle--open {
    position: absolute;
    top: 25px;
    right: 0;
    width: 24px;
    height: 18px;
    background: url("../img/mobile-menu.svg") no-repeat 0 0/cover; }
  .main-nav__toggle--close {
    position: absolute;
    top: 25px;
    right: 3px;
    width: 18px;
    height: 18px;
    background: url("../img/close.svg") no-repeat 0 0/cover; }
  .main-nav__toggle--close + .main-nav__menu {
    display: block; }

.main-nav--nojs .main-nav__menu {
  display: flex; }

.main-nav__toggle {
  display: none; }

