.page-main__title {
  font-size: 42px;
  font-family: "Arial", sans-serif;
  font-weight: bold;
  padding-left: 40px;
  padding-right: 40px; }

.about {
  position: relative;
  width: 100%; }
  .about::after {
    content: "";
    width: 100%;
    height: 30px;
    background-color: #85bf8f;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 2px solid #000; }

.about__text {
  width: 1200px;
  padding-bottom: 70px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 0 auto 40px; }

.section-title {
  font-size: 40px;
  font-family: "Georgia", sans-serif;
  font-weight: bold; }
  .section-title--gallery {
    font-weight: normal;
    font-style: italic;
    color: #6a3a0c;
    text-align: right;
    width: 1215px;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px; }
  .section-title--catalog {
    background: #85bf8f;
    padding: 10px 50px;
    border: 2px solid #000; }

.gallery {
  background: none;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap; }

.gallery::after {
  content: '';
  display: block;
  height: 0;
  clear: both; }

.gallery .container {
  width: 100%;
  margin: 50px 0; }

#gallery {
  display: flex;
  flex-wrap: wrap; }

.gallery .photo {
  width: 20%;
  box-sizing: border-box;
  position: relative;
  margin: 0 0 -5px; }
  .gallery .photo img {
    display: inline-block;
    max-width: 100%;
    height: auto; }

.gallery .photo a:hover {
  opacity: 0.5; }

.gallery .photo a {
  display: block; }

.gallery .photo a:after {
  content: '';
  background: transparent url("../img/eye-icon.png") no-repeat center;
  width: 52px;
  height: 35px;
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  visibility: hidden; }

.gallery .photo:hover > a:after {
  visibility: visible; }

.gallery .photo figcaption {
  color: #999999;
  text-align: center;
  margin-top: 20px; }

.gallery__text {
  max-width: 1300px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
  font-size: 20px; }

.catalog {
  position: relative;
  width: 100%;
  padding-bottom: 70px; }
  .catalog::after {
    content: "";
    width: 100%;
    height: 30px;
    background-color: #85bf8f;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 2px solid #000; }

.catalog__list {
  list-style: none;
  margin: 20px 30px 80px; }

.catalog__item {
  color: #6a3a0c;
  font-size: 24px;
  font-weight: bold; }

.catalog__columns {
  display: flex;
  align-items: flex-start;
  justify-content: space-between; }

.catalog__img {
  max-width: 50%;
  margin-top: -50px; }
  .catalog__img img {
    max-width: 100%;
    height: auto; }

.callback {
  text-align: center;
  background-color: #fff;
  width: 40%;
  height: auto;
  position: absolute;
  top: 20%;
  left: 50%;
  margin-left: -20%;
  padding: 30px;
  border: 3px solid #000;
  opacity: 0;
  transform: translateY(-200px);
  opacity: 0;
  z-index: -1; }
  .callback.open {
    opacity: 1;
    z-index: 101;
    transform: translateY(0);
    transition: 1s; }
  .callback h3 {
    font-size: 30px;
    text-transform: uppercase;
    margin-bottom: 30px;
    font-family: "Franklin Gothic Medium"; }

.callback__form {
  text-align: center; }

.callback__item {
  margin-bottom: 30px;
  width: 95%; }
  .callback__item label {
    display: block;
    width: 100%;
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    font-family: "Franklin Gothic Medium"; }
  .callback__item textarea {
    width: 100%;
    border: none;
    border-bottom: 2px solid #000;
    padding: 10px 15px;
    background-color: transparent; }
    .callback__item textarea:hover, .callback__item textarea:active {
      box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5); }
    .callback__item textarea:focus {
      border: none;
      border-bottom: 2px solid grey;
      background-color: transparent !important;
      outline: none; }

.callback__input {
  margin-bottom: 30px; }
  .callback__input label {
    display: block;
    width: 100%;
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    font-family: "Franklin Gothic Medium"; }
  .callback__input input {
    width: 100%;
    height: 30px;
    border: none;
    border-bottom: 2px solid #000;
    padding: 10px 15px;
    background-color: transparent; }
    .callback__input input:hover, .callback__input input:active {
      box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5); }
    .callback__input input:focus {
      border: none;
      border-bottom: 2px solid grey;
      background-color: transparent !important;
      outline: none; }

.callback__clouse {
  float: right;
  background: none;
  border: none;
  font-weight: bold; }
  .callback__clouse:hover, .callback__clouse:focus, .callback__clouse:active {
    outline: none;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.5); }

.loading p {
  margin: auto;
  font: 20px reg; }

.loading {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0; }

@keyframes blink {
  from {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  to {
    opacity: 1; } }

.loading p {
  animation: blink 0.7s infinite forwards ease-in-out; }

.animload {
  z-index: 1;
  opacity: 0.8; }

.error {
  background: #DC352F; }

.success {
  background: #36AE46; }

.msg {
  width: 340px;
  position: absolute;
  padding: 25px 15px;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 0;
  font: 16px med;
  color: #ffffff;
  text-align: center;
  border-radius: 7px;
  opacity: 0; }

.msgbox {
  position: absolute;
  bottom: 0;
  width: 100%; }

@keyframes shake {
  from {
    transform: translateX(0px); }
  50% {
    transform: translateX(3px); }
  to {
    transform: translateX(-3px); } }

.inputerror {
  border-color: #DC352F !important;
  animation: shake 0.15s 3 forwards ease-in-out; }
