.gallery {
  background: none;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
}

.gallery::after {
  content: '';
  display: block;
  height: 0;
  clear: both;
}

.gallery .container {
  width: 100%;
  margin: 50px 0;
}

#gallery {
  display: flex;
  flex-wrap: wrap;
}

.gallery .photo {
  width: 20%;
  box-sizing: border-box;
  position: relative;
  margin: 0 0 -5px;

  & img {
    display: inline-block;
    max-width: 100%;
    height: auto;
  }
}

.gallery .photo a:hover {
    opacity: 0.5;
}

.gallery .photo a {
    display: block;
}

.gallery .photo a:after {
    content: '';
    background: transparent url("../img/eye-icon.png") no-repeat center;
    width: 52px;
    height: 35px;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    visibility: hidden;
}

.gallery .photo:hover > a:after {
    visibility: visible;
}

.gallery .photo figcaption {
    color: #999999;
    text-align: center;
    margin-top: 20px;
}

.gallery__text {
  max-width: 1300px;
  margin: 0 auto;
  @include fields();
  font-size: 20px;
}
