.button {
  font-family: $accent-font;
  display: inline-block;
  text-align: center;
  padding: 12px 30px;
  color: #ffffff;
  background-color: #e2b776;
  text-decoration: none;
  text-transform: uppercase;
  font-style: italic;
  margin-left: 50px;

  &:hover,
  &:focus {
    background-color: $green-color;
    outline: none;
  }

  &:active {
    color: rgba(#ffffff, 0.302);
  }

  &--catalog {

  }
}
