.page-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 12px;
  max-width: 1300px;
  margin: 50px auto 52px;

  @include fields();
}

.page-header__link {
  display: flex;
  justify-content: space-between;
  width: 60%;
  align-items: baseline;
  margin-left: 20px;
}

.page-header__logo {
  margin-bottom: 13px;

  &--text {
    margin-bottom: 4px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}
