.about {
  position: relative;
  width: 100%;

  &::after {
    content: "";
    width: 100%;
    height: 30px;
    background-color: $green-color;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 2px solid #000;
  }
}

.about__text{
  width: 1200px;
  padding-bottom: 70px;
  @include fields();
  margin: 0 auto 40px;
}

